  get nameButtonAction(): string {
    let name = 'Gán nhân viên tư vấn'

    let status = this.$route.name
    if (status == 'cho-duyet') {
      name = 'Từ chối duyệt'
    }
    if (status == 'da-duyet') {
      name = 'Yêu cầu hiệu chỉnh'
    }
    if (status == 'yeu-cau-hieu-chinh') {
      name = 'Từ chối hiệu chỉnh'
    }

    return name
