  }

  get getRoleCbx(): boolean {
    if (this.$route.name === 'da-khop') {
      // return false
      return this.checkPemission('audit', ['cbx-action'])
    }
    if (this.$route.name === 'cho-duyet') {
      // return this.checkPemission('audit', ['edit-pending'])
      return false
    }
    if (this.$route.name === 'da-duyet') {
      return this.checkPemission('audit', ['cbx-action-da-duyet'])
    }
    if (this.$route.name === 'yeu-cau-hieu-chinh') {
      // return this.checkPemission('audit', ['edit-request'])
      return false
    }
    return true
  }

  @Watch('$route.name')
  changeTab(status: string): void {
    this.resetData()
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.params = assign(this.params, filters.filters)
      console.log(this.params)

      this.checkRouteStatus(status)
    } else {
      this.params = {
        search: '',
        page: 1,
        limit: 20,
        orderByClause: '1',
        searchType: 'CONTRACT'
      }
      this.checkRouteStatus(status)
    }

    const resetScroll = document.getElementsByClassName('el-table__body-wrapper')[0]
    if (resetScroll) {
      resetScroll.scrollTop = 0
      resetScroll.scrollLeft = 0
    }
    // this.setParamsForUser()
    this.init()
  }
  indexMethod(index: number): number {
    return (this.params.page - 1) * this.params.limit + index + 1
  }
  handleConflictClick(): void {
    this.isConflickClick = true
  }
  resetData(): void {
    this.dataSelect = []
    this.params.limit = 20
    this.params.page = 1
    this.query.page = this.params.page
    this.query.limit = this.params.limit
    this.dataPopup = {
      total: this.data.total,
      data: [],
      action: ''
    }
  }
  dataPopupAction: Record<string, any> = {
    title: '',
    label: '',
    code: '',
    contractNo: []
  }
  checkLastItemSelect = false
  checkReqToEdit = false
  paramsFilter = {
    noAudit: {},
    auditting: {},
    pending: {},
    approve: {},
    reqEdit: {}
  }
  handleRowClick(one: Record<string, any>): void {
    if (this.isConflickClick) {
      this.isConflickClick = false
      return
    }
    if (this.checkReqToEdit == true) {
      return
    }
    this.contractInfor = one.row
    this.dataPopup.action = 'detail'
    this.dataPopup.data = [one.row]
    this.dataPopup.total = this.dataPopup.data.length
    this.setOpenPopup({
      popupName: 'contractDetail',
      isOpen: true
    })
  }
  handlePrevPopupAudit(): void {
    if (this.paramPopupAudit.page > 1) {
      this.paramPopupAudit.page--
      this.apiContract
        ?.getData({
          ...this.params,
          ...this.paramPopupAudit
        })
        .then(list => {
          const key = 'bankContractNo'
          const listFilter = [...new Map(list.data.map((item: Record<string, any>) => [item[key], item])).values()]
          this.dataPopup.count = listFilter.length - 1
          this.dataPopup.data = [...listFilter, ...this.dataPopup.data]
        })
        .catch(err => this.$message.error(err.message))
    }
  }
  handleNextPopupAudit(): void {
    this.paramPopupAudit.page++
    this.apiContract
      ?.getData({
        ...this.params,
        ...this.paramPopupAudit
      })
      .then(list => {
        const key = 'bankContractNo'
        const listAll = [...this.dataPopup.data, ...list.data]
        const listFilter = [...new Map(listAll.map((item: Record<string, any>) => [item[key], item])).values()]
        this.dataPopup.data = listFilter
      })
      .catch(err => this.$message.error(err.message))
  }
  handleSizeChange(value: number): void {
    this.params.page = 1
    this.params.limit = value
    this.query.page = this.params.page
    this.query.limit = this.params.limit
    this.init()
    this.apiContract?.getData(this.params)
  }
  handleCurrentChange(value: number): void {
    this.params.page = value
    this.init()
  }
  handleOpentFilter(): void {
    this.setOpenPopup({
      popupName: 'audit-filter',
      isOpen: true
    })
  }

  handleCloseLoading(): void {
    setTimeout(() => {
      this.isLoading = false
    }, 1000)
  }
  handleCommand(command: Record<string, any>): void {
    forEach(this.option, item => {
      if (command.id === item.id) {
        item.active = true
      } else {
        item.active = false
      }
    })

    this.params.orderByClause = command.id
    this.init()
  }
  handleClickAction(): void {
    if (this.dataSelect.length < 1) {
      return
    }
    const value = 'advisoryStaff'
    this.dataPopupAction = {
      action: value,
      total: this.dataSelect.length,
      contractNo: this.dataSelect.map((item: any) => item.contractNo).join(','),
      bankContractNo: this.dataSelect.map((item: any) => item.bankContractNo).join(',')
    }
    this.dataPopupAction.code = value
    switch (this.$route.name) {
      case 'da-khop':
        this.dataPopupAction.title = 'Gán nhân viên tư vấn'
        this.dataPopupAction.label = 'Nhân viên tư vấn'
        this.setOpenPopup({
          popupName: 'audit-action',
          isOpen: true
        })
        break
      case 'cho-duyet':
        this.setOpenPopup({
          popupName: 'cancel-popup',
          isOpen: true
        })
        break
      case 'da-duyet':
        this.setOpenPopup({
          popupName: 'edit-popup',
          isOpen: true
        })
        break
      case 'yeu-cau-hieu-chinh':
        this.setOpenPopup({
          popupName: 'cancel-popup',
          isOpen: true
        })
        break
      case undefined:
        this.dataPopupAction.title = 'Gán nhân viên tư vấn'
        this.dataPopupAction.label = 'Nhân viên tư vấn'
        this.setOpenPopup({
          popupName: 'audit-action',
          isOpen: true
        })
        break
    }
  }
  handleChangeAction(value: string): void {
    if (this.dataSelect.length < 1) {
      return
    }

    this.dataPopupAction = {
      action: value,
      total: this.dataSelect.length,
      contractNo: this.dataSelect.map((item: any) => item.contractNo).join(','),
      bankContractNo: this.dataSelect.map((item: any) => item.bankContractNo).join(',')
    }
    this.dataPopupAction.code = value
    switch (value) {
      case 'introStaff':
        this.dataPopupAction.title = 'Gán nhân viên giới thiệu'
        this.dataPopupAction.label = 'Nhân viên giới thiệu'

        this.setOpenPopup({
          popupName: 'audit-action',
          isOpen: true
        })
        break
      case 'customerCif':
        this.dataPopupAction.title = 'Gán CIF khách hàng '
        this.dataPopupAction.label = 'Số CIF khách hàng '
        this.setOpenPopup({
          popupName: 'audit-action',
          isOpen: true
        })
        break
      case 'relationship':
        this.dataPopupAction.title = 'Gán Phân loại KH'
        this.dataPopupAction.label = 'Phân loại KH'
        this.dataPopupAction.optionsRelationship = this.listType
        this.setOpenPopup({
          popupName: 'audit-action',
          isOpen: true
        })
        break
      case 'request-approve':
        this.setOpenPopup({
          popupName: 'auditting-popup',
          isOpen: true
        })
        break
      case 'value-request-correction':
        this.setOpenPopup({
          popupName: 'edit-popup',
          isOpen: true
        })
        break
      case 'value-decline':
        this.setOpenPopup({
          popupName: 'cancel-popup',
          isOpen: true
        })
        break
      case 'value-aprrove':
        this.setOpenPopup({
          popupName: 'pending-popup',
          isOpen: true
        })
        break
    }
  }
  fileName = ''
  resetSelectAudit(): void {
    this.checkReqToEdit = false
    this.dataPopup.data = []
    this.dataSelect = []
    if (this.$refs.auditTable) {
      //@ts-ignore
      this.$refs.auditTable.clearSelection()
    }
  }
  checkRouteStatus(value: string): void {
    switch (value) {
      case 'dang-doi-soat':
        this.textButton = 'Yêu cầu duyệt'
        this.params.auditStatus = '2'
        this.fileName = 'dang_doi_soat'
        break
      case 'cho-duyet':
        this.textButton = 'Phê duyệt'
        this.params.auditStatus = '3'
        this.fileName = 'cho_duyet'
        break
      case 'da-duyet':
        this.textButton = 'Đã duyệt'
        this.params.auditStatus = '4'
        this.fileName = 'da_duyet'
        break
      case 'yeu-cau-hieu-chinh':
        this.textButton = 'Đã duyệt'
        this.params.auditStatus = '5'
        this.fileName = 'yeu_cau_hieu_chinh'
        break
      case 'da-khop':
        this.params.auditStatus = '9'
        this.fileName = 'da_khop'
        break
      case undefined:
        this.textButton = 'Đối soát'
        this.params.auditStatus = '1'
        this.fileName = 'chua_doi_soat'
        break
    }
  }
  getRelationShip(code: string | any): string {
    return RELATIONSHIP_VALUE_TO_TYPE[code]
  }
  async handleOpenAuditPopup(code: string): Promise<void> {
    this.dataSelect = []
    this.dataPopup.action = ''
    switch (code) {
      case 'da-khop':
        this.setOpenPopup({
          popupName: 'auditting-popup',
          isOpen: true
        })
        break
      default:
        this.dataPopup.action = ''
        await this.apiContract
          ?.getData({
            ...this.params,
            filterStatus: 1000,
            page: 1
          })
          .then(res => {
            if (res.data.length > 0) {
              this.dataPopup.data = res.data
              this.setOpenPopup({
                popupName: 'audit',
                isOpen: true
              })
            } else {
              this.$message.warning('Không có hợp đồng nào chưa đủ thông tin')
            }
          })
          .catch(err => this.$message.warning(err.message))

        break
    }
  }
  handleSearch(value: string): void {
    this.params.page = 1
    this.params.search = value
    // this.query.page = 1
    this.init()
  }
  listFilter: any = []
  checkCustomerType = false
  handleSelection(value: Array<Record<string, any>>, row: Record<string, any>): void {
    this.pageSize = this.params.limit
    //@ts-ignore
    this.listFilter = this.$refs.auditTable.getSelection()
    if (row) {
      const checked = value.find(item => item.id == row.id)
      if (checked && checked.id > 0) {
        let selection = this.dataTable.filter((item: any) => item.bankContractNo === row?.bankContractNo && item.id !== row?.id)
        //@ts-ignore
        this.$refs.auditTable.updateSelection(selection)
        //@ts-ignore
        this.dataSelect = this.$refs.auditTable.getSelection()
      } else {
        this.dataSelect = value.filter((item: any) => item.bankContractNo !== row?.bankContractNo)
        //@ts-ignore
        this.$refs.auditTable.clearSelection()
        //@ts-ignore
        this.$refs.auditTable.updateSelection(this.dataSelect)
      }
      const lastBankContractNo = this.dataTable[this.dataTable.length - 1].bankContractNo
      const checkLast = this.dataSelect.some(item => item.bankContractNo == lastBankContractNo)
      if (checkLast) {
        this.checkLastItemSelect = true
      } else {
        this.checkLastItemSelect = false
      }
    } else {
      this.dataSelect = value
    }
    let a = this.listFilter.filter((item: any) => item.customerType == '1' || item.customerType == '4')
    if (a.length > 0) {
      this.checkCustomerType = true
    } else {
      this.checkCustomerType = false
    }
    console.log('se', this.checkCustomerType)
  }
  handleOpenFilter(): void {
    this.setOpenPopup({
      popupName: 'audit-popup-filter',
      isOpen: true
    })
  }
  filters = {
    name: 'Bộ lọc'
  }
  handleClickReqEdit(value: Record<string, any>): void {
    this.checkReqToEdit = true
    this.dataSelect = this.dataTable.filter((item: Record<string, any>) => item.bankContractNo == value.bankContractNo)
    this.setOpenPopup({
      popupName: 'edit-popup',
      isOpen: true
    })
  }
  handleFilter(value: Record<string, any>): void {
    this.params = {
      ...this.params,
      ...value
    }
    this.params.page = 1
    this.init()
  }
  url = ''
  async handleOpenExportFile(): Promise<void> {
    if (this.dataTable.length < 1) {
      this.$message.warning('Không tìm thấy dữ liệu xuất file')
      return
    }
    if (this.query.total >= 50000) {
      this.$message.warning('Số lượng bản ghi vượt quá 50000, vui lòng sử dụng bộ lọc')
      return
    }
    this.isLoadingButton = true
    let response: any
    const date = new Date()
    const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}_${date.getHours()}${date.getMinutes()}${date.getSeconds()}`
    delete this.params.newAuditStatus
    await this.apiExport
      ?.exportAuditExcel(this.params)
      .then(data => {
        response = data
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', `Danh_sach_${this.fileName}_${time}.xlsx`)
        document.body.appendChild(link)
        link.click()
        this.isLoadingButton = false
      })
      .catch(() => {
        this.isLoadingButton = false
      })
  }

  async init(): Promise<void> {
    this.isLoading = true
    this.dataSelect = []
    this.checkRouteStatus(this.$route.name as string)
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      this.params = assign(this.params, filters.filters)
    }
    if (!this.params.page) {
      this.params.page = 1
    }
    if (!this.params.limit) {
      this.params.limit = 20
    }
    if (!this.params.orderByClause) {
      this.params.orderByClause = '1'
    }
    this.dataPopup.action = ''
    this.query.page = this.params.page
    this.query.limit = this.params.limit
    delete this.params.newAuditStatus

    await this.apiContract
      ?.getData(this.params)
      .then((list: Record<string, any>) => {
        this.data = list
        this.dataTable = this.data.data
        this.isLoading = false
      })
      .catch(() => {
        this.dataTable = []
        this.isLoading = false
      })
    await this.apiContract
      ?.getContractCount(this.params)
      .then(count => {
        this.query.total = +count.data
        this.isLoading = false
      })
      .catch(() => {
        this.query.total = 0
        this.isLoading = false
      })
  }

  setParamsForUser(): void {
    if (this.getDisableArea) {
      this.params.areaIdArray = `${this.user?.areaId}`
      this.params.branchIdArray = `${this.user?.branchId}`
    }
    if (this.getDisableTransaction) {
      this.params.transOfficeIdArray = `${this.user?.transactionOfficeId}`
    }
    if (this.getDisableSaleMember) {
      this.params.salesMemberIdArray = `${this.user?.id}`
    }
  }
  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiExport = getRepository('export', {
      districtId: this.user?.districts?.[0].id
    })
    this.init()
    this.apiParam?.getParamRelationship().then(list => (this.listType = list))
  }

  handleActionAudit(status: string, value: Record<string, any>): void {
    if (status == 'audit') {
      this.dataPopup.data = [value]
      this.setOpenPopup({
        popupName: 'audit',
        isOpen: true
      })
    } else {
      this.dataSelect = this.dataTable.filter((item: Record<string, any>) => item.bankContractNo == value.bankContractNo)
      const lastBankContractNo = this.dataTable[this.dataTable.length - 1].bankContractNo
      const checkLast = this.dataSelect.some(item => item.bankContractNo == lastBankContractNo)
      if (checkLast) {
        this.checkLastItemSelect = true
      } else {
        this.checkLastItemSelect = false
      }
      this.setOpenPopup({
        popupName: 'auditting-popup',
        isOpen: true
      })
    }
  }

  handleUpdatePage(): void {
    this.init()
  }
}
